.popup {
  display: grid;
  visibility: hidden;
  opacity: 0;
  justify-content: center;
  align-items: center;
  z-index: 20;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  left: 0;
  top: 0;
  cursor: pointer;
}

.popup__diploma {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  z-index: 30;
}

.popup__form {
  grid-template-rows: 1fr 3fr;
}

.popup__image {
  display: block;
  max-width: 75vw;
  max-height: 75vh;
}

.popup__header {
  color: #fff;
  font-size: 20px;
  font-weight: normal;
  justify-self: center;
  align-self: end;
}

.popup_background_opacity {
  background-color: rgba(0, 0, 0, .9);
}

.popup_opened {
  visibility: visible;
  opacity: 1;
  transition: visibility .95s, opacity .7s linear;
}

.popup_background_blue {
  background-color: #283345;
}
